
import $ from "jquery";
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Search from "@/components/search.vue"
import warehousSearch from "./search.vue"
export default {
data() {
return {
show: false,
startDate: "",
endDate: "",
visibility: false,
visibility2: false,
widths: 60,
tableHead: ["单据日期", "入库单号", "仓库", "币别", "仓库员"]
};
},
directives: {
TransferDom
},
components: {
Datetime,
Group,
Popup,
Search,
warehousSearch
},
mounted() {
var obj=$(".to");

obj.click(function(e){
alert('asda');
console.log($(this).text())
});
this.widths = $(window).width() + 'px'
console.log($(window).width());
this.$nextTick(() => {
this.init();
});
},
methods: {
log() {

},
toInfo(e) {
console.log(e)

},
change() {},
init() {
var win = $(window),
scrollAreaEl = $(".t_r_content"),
leftFreezeEl = $(".t_l_freeze"),
leftTableEl = leftFreezeEl.find("table"),
rightTableEl = $(".t_r_t table");


function adjustHeight() {
var winHeight = win.height(),
tableHeight =10*40;
leftFreezeEl.height(tableHeight);
scrollAreaEl.height(tableHeight);
}

adjustHeight();
win.on("resize", adjustHeight);


var myScroll = new IScroll(".t_r_content", {
scrollX: true,
scrollY: true,
probeType: 3
});


scrollAreaEl.on("touchmove mousewheel", function(e) {
e.preventDefault();
});


myScroll.on("scroll", updatePosition);
myScroll.on("scrollEnd", updatePosition);

function updatePosition() {
var a = this.y;
var b = this.x;
leftTableEl.css(
"transform",
"translate(0px, " + a + "px) translateZ(0px)"
);
rightTableEl.css(
"transform",
"translate(" + b + "px, 0px) translateZ(0px)"
);
}
}
}
};



